document.addEventListener('DOMContentLoaded', function() {
    iniciarApp();

});

function iniciarApp() {
    eventListeners();
    lupa();
    scrollTo();
    book();

}

function book() {
    const flipcard = document.querySelector('.flipcard');
    flipcard.addEventListener('click', () => {
        flipcard.classList.toggle('flipcard-flipped');
    });
}


//ScrollNav



function scrollTo(e) {
    // const navLinks = document.querySelectorAll(".main-nav a");

    // navLinks.forEach(navLink => {
    //     navLink.addEventListener("click", scrollTo);
    // });
    // e.preventDefault();
    // const href = this.getAttribute("href");
    // // We use the scrollIntoView to do the smoothscroll.
    // document.querySelector(href).scrollIntoView({
    //     block: "start",
    //     behavior: "smooth"
    // });
    if (document.title === "Rocío Márquez - Therapist") {
        const navLinks = document.querySelectorAll(".main-nav a");

        navLinks.forEach(navLink => {
            navLink.addEventListener("click", scrollTo);
        });
        e.preventDefault();

        const href = this.getAttribute("href");
        // We use the scrollIntoView to do the smoothscroll.
        document.querySelector(href).scrollIntoView({
            block: "start",
            behavior: "smooth"
        });
    }
}

function lupa() {
    // const lupa = document.querySelector('#lupa');
    // const lupaVisor = document.querySelector('#lupa-visor');
    // const imagen = document.querySelector('#imagen');

    // lupa.addEventListener('mousemove', (e) => {
    //     lupaVisor.style.left = `${e.pageX - lupaVisor.offsetWidth / 2}px`;
    //     lupaVisor.style.top = `${e.pageY - lupaVisor.offsetHeight / 2}px`;

    //     imagenLupa.style.left = `${e.offsetX * -2}px`;
    //     imagenLupa.style.top = `${e.offsetY * -2}px`;
    // });
    const flipcard = document.querySelector(".flipcard");
    let zoomActive = false;

    flipcard.addEventListener("click", function() {
        if (!zoomActive) {
            flipcard.style.transform = "scale(1.6)";
            zoomActive = true;
        } else {
            flipcard.style.transform = "scale(1)";
            zoomActive = false;
        }
    });
}

//Mobile menu
function eventListeners() {
    const mobileMenu = document.querySelector('.mobile-menu');

    mobileMenu.addEventListener('click', function() {
        responsiveNav();
    })
}

function responsiveNav() {
    const navegation = document.querySelector('.main-nav');

    if (navegation.classList.contains('mostrar')) {
        navegation.classList.remove('mostrar');
    } else {
        navegation.classList.add('mostrar');
    }
}